/*--------------------------------------------------------------------------------------------
*
*  Global
*
*--------------------------------------------------------------------------------------------*/
/* Horizontal List */
.acf-hl {
  padding: 0;
  margin: 0;
  list-style: none;
  display: block;
  position: relative;
}

.acf-hl > li {
  float: left;
  display: block;
  margin: 0;
  padding: 0;
}

.acf-hl > li.acf-fr {
  float: right;
}

/* Horizontal List: Clearfix */
.acf-hl:before, .acf-hl:after,
.acf-bl:before, .acf-bl:after,
.acf-cf:before, .acf-cf:after {
  content: "";
  display: block;
  line-height: 0;
}

.acf-hl:after,
.acf-bl:after,
.acf-cf:after {
  clear: both;
}

/* Block List */
.acf-bl {
  padding: 0;
  margin: 0;
  list-style: none;
  display: block;
  position: relative;
}

.acf-bl > li {
  display: block;
  margin: 0;
  padding: 0;
  float: none;
}

/* Visibility */
.acf-hidden {
  display: none !important;
}

.acf-empty {
  display: table-cell !important;
}
.acf-empty * {
  display: none !important;
}

/* Float */
.acf-fl {
  float: left;
}

.acf-fr {
  float: right;
}

.acf-fn {
  float: none;
}

/* Align */
.acf-al {
  text-align: left;
}

.acf-ar {
  text-align: right;
}

.acf-ac {
  text-align: center;
}

/* loading */
.acf-loading,
.acf-spinner {
  display: inline-block;
  height: 20px;
  width: 20px;
  vertical-align: text-top;
  background: transparent url(../../images/spinner.gif) no-repeat 50% 50%;
}

/* spinner */
.acf-spinner {
  display: none;
}

.acf-spinner.is-active {
  display: inline-block;
}

/* WP < 4.2 */
.spinner.is-active {
  display: inline-block;
}

/* required */
.acf-required {
  color: #f00;
}

/* show on hover */
.acf-soh .acf-soh-target {
  -webkit-transition: opacity 0.25s 0s ease-in-out, visibility 0s linear 0.25s;
  -moz-transition: opacity 0.25s 0s ease-in-out, visibility 0s linear 0.25s;
  -o-transition: opacity 0.25s 0s ease-in-out, visibility 0s linear 0.25s;
  transition: opacity 0.25s 0s ease-in-out, visibility 0s linear 0.25s;
  visibility: hidden;
  opacity: 0;
}

.acf-soh:hover .acf-soh-target {
  -webkit-transition-delay: 0s;
  -moz-transition-delay: 0s;
  -o-transition-delay: 0s;
  transition-delay: 0s;
  visibility: visible;
  opacity: 1;
}

/* show if value */
.show-if-value {
  display: none;
}

.hide-if-value {
  display: block;
}

.has-value .show-if-value {
  display: block;
}

.has-value .hide-if-value {
  display: none;
}

/* select2 WP animation fix */
.select2-search-choice-close {
  -webkit-transition: none;
  -moz-transition: none;
  -o-transition: none;
  transition: none;
}

/*---------------------------------------------------------------------------------------------
*
*  tooltip
*
*---------------------------------------------------------------------------------------------*/
/* tooltip */
.acf-tooltip {
  background: #2F353E;
  border-radius: 5px;
  color: #fff;
  padding: 5px 10px;
  position: absolute;
  font-size: 12px;
  line-height: 1.4em;
  z-index: 900000;
  /* tip */
  /* positions */
}
.acf-tooltip:before {
  border: solid;
  border-color: transparent;
  border-width: 6px;
  content: "";
  position: absolute;
}
.acf-tooltip.top {
  margin-top: -8px;
}
.acf-tooltip.top:before {
  top: 100%;
  left: 50%;
  margin-left: -6px;
  border-top-color: #2F353E;
  border-bottom-width: 0;
}
.acf-tooltip.right {
  margin-left: 8px;
}
.acf-tooltip.right:before {
  top: 50%;
  margin-top: -6px;
  right: 100%;
  border-right-color: #2F353E;
  border-left-width: 0;
}
.acf-tooltip.bottom {
  margin-top: 8px;
}
.acf-tooltip.bottom:before {
  bottom: 100%;
  left: 50%;
  margin-left: -6px;
  border-bottom-color: #2F353E;
  border-top-width: 0;
}
.acf-tooltip.left {
  margin-left: -8px;
}
.acf-tooltip.left:before {
  top: 50%;
  margin-top: -6px;
  left: 100%;
  border-left-color: #2F353E;
  border-right-width: 0;
}
.acf-tooltip .acf-overlay {
  z-index: -1;
}

/* confirm */
.acf-tooltip.-confirm {
  z-index: 900001;
}
.acf-tooltip.-confirm a {
  text-decoration: none;
  color: #9ea3a8;
}
.acf-tooltip.-confirm a:hover {
  text-decoration: underline;
}
.acf-tooltip.-confirm a[data-event=confirm] {
  color: #F55E4F;
}

.acf-overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  cursor: default;
}

.acf-tooltip-target {
  position: relative;
  z-index: 900002;
}

/*---------------------------------------------------------------------------------------------
*
*  loading
*
*---------------------------------------------------------------------------------------------*/
.acf-loading-overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  cursor: default;
  z-index: 99;
  background: rgba(249, 249, 249, 0.5);
}
.acf-loading-overlay i {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/*--------------------------------------------------------------------------------------------
*
*	acf-icon
*
*--------------------------------------------------------------------------------------------*/
.acf-icon {
  display: inline-block;
  height: 28px;
  width: 28px;
  border: transparent solid 1px;
  border-radius: 100%;
  font-size: 20px;
  line-height: 21px;
  text-align: center;
  text-decoration: none;
  vertical-align: top;
  box-sizing: border-box;
}
.acf-icon:before {
  font-family: dashicons;
  display: inline-block;
  line-height: 1;
  font-weight: 400;
  font-style: normal;
  speak: none;
  text-decoration: inherit;
  text-transform: none;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 1em;
  height: 1em;
  vertical-align: middle;
  text-align: center;
}

.acf-icon.-plus:before {
  content: "\f543";
}

.acf-icon.-minus:before {
  content: "\f460";
}

.acf-icon.-cancel:before {
  content: "\f335";
}

.acf-icon.-pencil:before {
  content: "\f464";
}

.acf-icon.-location:before {
  content: "\f230";
}

.acf-icon.-up:before {
  content: "\f343";
  margin-top: -0.1em;
}

.acf-icon.-down:before {
  content: "\f347";
  margin-top: 0.1em;
}

.acf-icon.-left:before {
  content: "\f341";
  margin-left: -0.1em;
}

.acf-icon.-right:before {
  content: "\f345";
  margin-left: 0.1em;
}

.acf-icon.-sync:before {
  content: "\f463";
}

.acf-icon.-globe:before {
  content: "\f319";
  margin-top: 0.1em;
  margin-left: 0.1em;
}

.acf-icon.-picture:before {
  content: "\f128";
}

.acf-icon.-check:before {
  content: "\f147";
  margin-left: -0.1em;
}

.acf-icon.-dot-3:before {
  content: "\f533";
  margin-top: -0.1em;
}

.acf-icon.-arrow-combo:before {
  content: "\f156";
}

.acf-icon.-arrow-up:before {
  content: "\f142";
  margin-left: -0.1em;
}

.acf-icon.-arrow-down:before {
  content: "\f140";
  margin-left: -0.1em;
}

.acf-icon.-search:before {
  content: "\f179";
}

.acf-icon.-link-ext:before {
  content: "\f504";
}

.acf-icon.-duplicate {
  position: relative;
}
.acf-icon.-duplicate:before, .acf-icon.-duplicate:after {
  content: "";
  display: block;
  box-sizing: border-box;
  width: 46%;
  height: 46%;
  position: absolute;
  top: 33%;
  left: 23%;
}
.acf-icon.-duplicate:before {
  margin: -1px 0 0 1px;
  box-shadow: 2px -2px 0px 0px currentColor;
}
.acf-icon.-duplicate:after {
  border: solid 2px currentColor;
}

.acf-icon.-collapse:before {
  content: "\f142";
  margin-left: -0.1em;
}

.-collapsed .acf-icon.-collapse:before {
  content: "\f140";
  margin-left: -0.1em;
}

span.acf-icon {
  color: #555d66;
  border-color: #b5bcc2;
  background-color: #fff;
}

a.acf-icon {
  color: #555d66;
  border-color: #b5bcc2;
  background-color: #fff;
  position: relative;
  transition: none;
  cursor: pointer;
}
a.acf-icon:hover {
  background: #f3f5f6;
  border-color: #0071a1;
  color: #0071a1;
}
a.acf-icon.-minus:hover, a.acf-icon.-cancel:hover {
  background: #f7efef;
  border-color: #a10000;
  color: #dc3232;
}
a.acf-icon:active, a.acf-icon:focus {
  outline: none;
  box-shadow: none;
}

.acf-icon.-clear {
  border-color: transparent;
  background: transparent;
  color: #444;
}

.acf-icon.light {
  border-color: transparent;
  background: #F5F5F5;
  color: #23282d;
}

.acf-icon.dark {
  border-color: transparent !important;
  background: #23282D;
  color: #eee;
}

a.acf-icon.dark:hover {
  background: #191E23;
  color: #00b9eb;
}
a.acf-icon.dark.-minus:hover, a.acf-icon.dark.-cancel:hover {
  color: #D54E21;
}

.acf-icon.grey {
  border-color: transparent !important;
  background: #b4b9be;
  color: #fff !important;
}
.acf-icon.grey:hover {
  background: #00A0D2;
  color: #fff;
}
.acf-icon.grey.-minus:hover, .acf-icon.grey.-cancel:hover {
  background: #32373C;
}

.acf-icon.small,
.acf-icon.-small {
  width: 20px;
  height: 20px;
  line-height: 14px;
  font-size: 14px;
}
.acf-icon.small.-duplicate:before, .acf-icon.small.-duplicate:after,
.acf-icon.-small.-duplicate:before,
.acf-icon.-small.-duplicate:after {
  opacity: 0.8;
}

