/*--------------------------------------------------------------------------------------------
*
*	acf-notice
*
*--------------------------------------------------------------------------------------------*/
.acf-notice {
  position: relative;
  display: block;
  color: #fff;
  margin: 5px 0 15px;
  padding: 3px 12px;
  background: #2a9bd9;
  border-left: #1f7db1 solid 3px;
}
.acf-notice p {
  font-size: 13px;
  line-height: 1.5;
  margin: 0.5em 0;
  text-shadow: none;
  color: inherit;
}
.acf-notice .acf-notice-dismiss {
  position: absolute;
  top: 9px;
  right: 12px;
  background: transparent !important;
  color: inherit !important;
  border-color: #fff !important;
  opacity: 0.75;
}
.acf-notice .acf-notice-dismiss:hover {
  opacity: 1;
}
.acf-notice.-dismiss {
  padding-right: 40px;
}
.acf-notice.-error {
  background: #d94f4f;
  border-color: #c92c2c;
}
.acf-notice.-success {
  background: #49ad52;
  border-color: #3a8941;
}
.acf-notice.-warning {
  background: #fd8d3b;
  border-color: #fc7009;
}

/*--------------------------------------------------------------------------------------------
*
*	acf-table
*
*--------------------------------------------------------------------------------------------*/
.acf-table {
  border: #ccd0d4 solid 1px;
  background: #fff;
  border-spacing: 0;
  border-radius: 0;
  table-layout: auto;
  padding: 0;
  margin: 0;
  width: 100%;
  clear: both;
  box-sizing: content-box;
  /* defaults */
  /* thead */
  /* tbody */
  /* -clear */
}
.acf-table > tbody > tr > th, .acf-table > tbody > tr > td,
.acf-table > thead > tr > th,
.acf-table > thead > tr > td {
  padding: 8px;
  vertical-align: top;
  background: #fff;
  text-align: left;
  border-style: solid;
  font-weight: normal;
}
.acf-table > tbody > tr > th,
.acf-table > thead > tr > th {
  position: relative;
  color: #333333;
}
.acf-table > thead > tr > th {
  border-color: #d5d9dd;
  border-width: 0 0 1px 1px;
}
.acf-table > thead > tr > th:first-child {
  border-left-width: 0;
}
.acf-table > tbody > tr {
  z-index: 1;
}
.acf-table > tbody > tr > td {
  border-color: #eeeeee;
  border-width: 1px 0 0 1px;
}
.acf-table > tbody > tr > td:first-child {
  border-left-width: 0;
}
.acf-table > tbody > tr:first-child > td {
  border-top-width: 0;
}
.acf-table.-clear {
  border: 0 none;
}
.acf-table.-clear > tbody > tr > td, .acf-table.-clear > tbody > tr > th,
.acf-table.-clear > thead > tr > td,
.acf-table.-clear > thead > tr > th {
  border: 0 none;
  padding: 4px;
}

/* remove tr */
.acf-remove-element {
  -webkit-transition: all 0.25s ease-out;
  -moz-transition: all 0.25s ease-out;
  -o-transition: all 0.25s ease-out;
  transition: all 0.25s ease-out;
  transform: translate(50px, 0);
  opacity: 0;
}

/* fade-up */
.acf-fade-up {
  -webkit-transition: all 0.25s ease-out;
  -moz-transition: all 0.25s ease-out;
  -o-transition: all 0.25s ease-out;
  transition: all 0.25s ease-out;
  transform: translate(0, -10px);
  opacity: 0;
}

